import React, { useState, useEffect } from 'react';
import Form from './components/Form';
import PaymentModal from './components/PaymentModal';
import AnalysisSection from './components/AnalysisSection';
import './App.css';
import api from './api';

function App() {
  const [analysis, setAnalysis] = useState({
    preview: '',
    full: '',
    orderId: null,
    qrcodeUrl: '',
    paid: false
  });
  const [showPayment, setShowPayment] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);


  const handleSubmit = async (eightCharacters) => {
    try {
      setLoading(true);
      setError(null);
      console.log('前端数据：', eightCharacters);
      // 创建订单并获取支付信息
      const eventSource = api.createOrderStream(eightCharacters);
      eventSource.onopen = () => {
        console.log('SSE连接已建立');
        setLoading(true);
      };



    eventSource.addEventListener('analysis', (e) => {
      const data = JSON.parse(e.data);
      // if (!data.content) return;
      setAnalysis(prev => ({
        ...prev,
        preview: prev.preview + data.content
      }));
      setLoading(false);
    });

    // 接收支付信息
    eventSource.addEventListener('payment', (e) => {
      const data = JSON.parse(e.data);
      setAnalysis(prev => ({
        ...prev,
        orderId: data.orderId,
        qrcodeUrl: data.qrcodeUrl,
        // 保留当前预览内容
        preview: prev.preview || '' 
      }));
      setShowPayment(false);
    });

    // 处理正常结束
    eventSource.addEventListener('end', () => {
      console.log('SSE连接正常关闭');
      eventSource.close();
      setLoading(false);
    });

    // 错误处理
    eventSource.onerror = (err) => {
      console.error('SSE连接错误:', err);
      setError('连接异常，请刷新重试');
      eventSource.close();
      setLoading(false);
    };

    // 组件卸载时关闭连接
    return () => {
      if (eventSource) {
        eventSource.close();
      }
    };

  } catch (err) {
    setError(err.message || '提交失败，请重试');
    setLoading(false);
  }
};


  // 支付状态轮询
  useEffect(() => {
    if (!analysis?.orderId || analysis.paid) return;

    const interval = setInterval(async () => {
      try {
        const { data: status } = await api.checkOrderStatus(analysis.orderId);
        console.log("付款判断：", status.paid)
        //修改！！
        if (status.paid) {
          console.log("支付判断")
          const { data: Analysis } = await api.getFullAnalysis(analysis.orderId);
          const pendingContent = Analysis
          .join('')                  // 合并所有字符串
          .replace(/null/g, '')      // 删除所有null字面量
          .trim();
          const fullAnalysis =analysis.preview + pendingContent;
          setAnalysis(prev => ({
            ...prev,
            full: fullAnalysis,
            paid: true
          }));
          console.log("全结果：", fullAnalysis)
          clearInterval(interval);
        }
      } catch (err) {
        console.error('支付状态检查失败:', err);
      }
    }, 3000);

    return () => clearInterval(interval);
  }, [analysis]);


  return (
    <div className="App">
      <header className="header">
        <div className="header-content">
          <div className="branding">
            <h1>LiMing命理</h1>
            <p className="slogan">一阴一阳之谓道</p>
          </div>
          <div className="logo-container">
            <img src="/logo.png" alt="Logo" className="logo" />
          </div>
        </div>
      </header>

      <main className="content">
        {error && <div className="error-message">{error}</div>}
        
        {loading ? (
          <div className="loading">正在生成分析...</div>
        ) : (
          !analysis.preview ? (
            <Form onSubmit={handleSubmit} />
          ) : (
            <AnalysisSection 
              preview={analysis.preview}
              full={analysis.full}
              isPaid={analysis.paid}
              onUnlock={() => setShowPayment(true)}
            />
          )
        )}

        <PaymentModal
          isOpen={showPayment}
          qrcodeUrl={analysis?.qrcodeUrl}
          onClose={() => setShowPayment(false)}
        />
      </main>

      <footer>
        <div className="sfooterContent">
          <p className="sdisclaimer">
            拒绝向未成年人提供服务，仅供娱乐和参考
          </p>
          <p className="sdisclaimer">
          备案号：
          </p>
        </div>
      </footer>

    </div>
    
  );
}

export default App;
