import React from 'react';
import styles from './analysis.module.css';
import Reactmarkdown from "react-markdown"

const AnalysisSection = ({ preview, full, isPaid, onUnlock }) => {
  return (
    <div >
      <div className={styles.previewSection}>
        {/* <Reactmarkdown>{preview}</Reactmarkdown> */}
        
        {!isPaid  && !full && (
          <>
            <Reactmarkdown>{preview}</Reactmarkdown>
            <div className={styles.blurOverlay}></div>
            <button className={styles.unlockBtn} onClick={onUnlock}>
              解锁完整人生分析（¥4.99）
            </button>
          </>
        )}
      </div>

      {isPaid && full && (
        <>
          <div className={styles.fullAnalysis}>
            <div className={styles.phase}>
              <Reactmarkdown>{full}</Reactmarkdown>
            </div>
          </div>

          <div className={styles.legalNotice}>
            <p>※ 本分析由大语言模型提供，仅供娱乐参考，不作为决策依据</p>
            <p>※ 根据相关法规，所有数据将在24小时后自动删除</p>
          </div>
        </>
      )}
    </div>
  );
};

export default AnalysisSection;
