import axios from 'axios';

// 创建带配置的axios实例
const api = axios.create({
  baseURL: process.env.REACT_APP_API_BASE,
  // timeout: 10000, // 请求超时设置
  headers: {
    'Content-Type': 'application/json'
  }
});

/**
 * 创建SSE连接（带错误处理）
 * @param {string} endpoint - API端点路径
 * @param {Object} params - 查询参数对象
 * @returns {EventSource} SSE连接实例
 */
const createSSEConnection = (endpoint, params = {}) => {
  const queryParams = new URLSearchParams(params);
  const url = queryParams.toString() 
    ? `${api.defaults.baseURL}${endpoint}?${queryParams}`
    : `${api.defaults.baseURL}${endpoint}`;

  const eventSource = new EventSource(url);

  // 错误处理回调
  const handleError = (error) => {
    console.error('SSE连接错误:', {
      endpoint,
      error: error.event?.detail || error.message,
      readyState: eventSource.readyState
    });
    
    if (eventSource.readyState === EventSource.CLOSED) {
      eventSource.close();
    }
  };

  eventSource.addEventListener('error', handleError);

  return eventSource;
};

// 开发环境调试输出
if (process.env.NODE_ENV === 'development') {
  console.debug('[API配置] 基础URL:', api.defaults.baseURL);
}

const apiService = {
  /**
   * 创建订单分析流
   * @param {string} eightCharacters - 生辰八字
   * @returns {EventSource} SSE连接实例
   */
  createOrderStream: (eightCharacters) => 
    createSSEConnection('/orders/stream', { eightCharacters }),

  /**
   * 创建剩余内容获取流
   * @param {string} orderId - 订单ID
   * @returns {EventSource} SSE连接实例
   */
  getFullAnalysis: (orderId) => api.get(`/analysis/${orderId}/full`),
  /**
   * 检查订单状态
   * @param {string} orderId - 订单ID
   * @returns {Promise<object>} 包含订单状态的Promise
   */
  checkOrderStatus: async (orderId) => api.get(`/orders/${orderId}/status`),

};

export default apiService;
